import { Editor, BeadTypes } from "../editorObjects/Editor";
import { ImmerReducer } from "immer-reducer";

export class ToolbarImmerReducer extends ImmerReducer<Editor> {
  action_changeBeadFill(color: string) {
    Object.keys(this.draftState.ui.selectedBeadsIds).forEach(beadId => {
      this.draftState.appData.beads[beadId].fill = color;
    });
  }

  action_changeBeadType(type: BeadTypes) {
    Object.keys(this.draftState.ui.selectedBeadsIds).forEach(beadId => {
      this.draftState.appData.beads[beadId].type = type;
    });
  }
  action_onBeadRotate(beadId, {deg}){}
  action_onBeadsRotate({deg}){}
}
