import React from "react";
import { useSelector } from "react-redux";
import { DisplayGroupBeads } from "./DisplayGroupBeads";
import {
  getSelectedBeads,
  getGroupPositionFromBeads,
} from "./getGroupPositionFromBeads";

const DisplayBeadsGroup = ({ selectedBeadsIdsCount }) => {
  const selectedBead = useSelector(getSelectedBeads);
  const location = getGroupPositionFromBeads(selectedBead);

  return <DisplayGroupBeads location={location} />;
};

export const MemoizedDisplayBeadsGroup = React.memo(DisplayBeadsGroup);
