import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { Editor, EditorMode } from "../../editorObjects/Editor";
import { Actions } from "../../store";
import {
  getSelectedLines,
  getGroupPositionFromLines,
} from "../operations/getGroupPositionFromLines";

export const ChangeLinePosition = ({ mode }) => {
  if (mode === EditorMode.EditLines) {
    return <ChangePositionToolbar />;
  }

  return <React.Fragment />;
};

const ChangePositionToolbar = () => {
  const selectedLinesIdsCount = useSelector((state: Editor) => {
    return state.ui.selectedLinesIdsCount;
  });

  if (selectedLinesIdsCount >= 2) {
    return (
      <DisplayGroupLinePosition selectedLinesIdsCount={selectedLinesIdsCount} />
    );
  } else if (selectedLinesIdsCount == 1) {
    return <DisplayLinePosition />;
  } else {
    return <React.Fragment />;
  }
};

const DisplayLinePosition = () => {
  const selectedLines = useSelector(getSelectedLines);
  const location = getGroupPositionFromLines(selectedLines);
  const dispatch = useDispatch();
  const onChangeLocation = (deltaX, deltaY) => {
    const lineId = selectedLines[0].id;
    dispatch(
      Actions.Draggable.action_onLineDrag(lineId, {
        deltaX,
        deltaY,
      })
    );
  };
  return (
    <React.Fragment>
      <InputNumberElement
        label={"X"}
        value={location.x}
        onChange={newValue => {
          onChangeLocation(location.x - newValue, 0);
        }}
        isDisabled={false}
      />
      <InputNumberElement
        label={"Y"}
        value={location.y}
        onChange={newValue => {
          onChangeLocation(0, location.y - newValue);
        }}
        isDisabled={false}
      />
    </React.Fragment>
  );
};

const DisplayGroupLinePosition = ({ selectedLinesIdsCount }) => {
  const selectedLines = useSelector(getSelectedLines);
  const location = getGroupPositionFromLines(selectedLines);
  const dispatch = useDispatch();
  const onChangeLocation = (deltaX, deltaY) => {
    dispatch(Actions.Draggable.action_onGroupLineDrag({ deltaX, deltaY }));
  };

  return (
    <React.Fragment>
      <InputNumberElement
        label={"X"}
        value={location.x}
        onChange={newValue => {
          onChangeLocation(location.x - newValue, 0);
        }}
        isDisabled={false}
      />
      <InputNumberElement
        label={"Y"}
        value={location.y}
        onChange={newValue => {
          onChangeLocation(0, location.y - newValue);
        }}
        isDisabled={false}
      />
      <InputNumberElement
        label={"W"}
        value={location.w}
        onChange={() => {}}
        isDisabled={true}
      />
      <InputNumberElement
        label={"H"}
        value={location.h}
        onChange={() => {}}
        isDisabled={true}
      />
    </React.Fragment>
  );
};

const InputNumberElement = ({ label, value, onChange, isDisabled }) => {
  return (
    <div className={"fieldset"}>
      <span className="label">{label}</span>
      <input
        className="input"
        type="number"
        disabled={isDisabled}
        min="0"
        max="99999"
        pattern="\d*"
        step="1"
        value={value}
        onChange={event => onChange(event.target.value)}
      />
    </div>
  );
};
