import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Editor, EditorMode } from "../../editorObjects/Editor";
import { Actions } from "../../store";

export const PickBeadColor = ({ mode }) => {
  if (mode === EditorMode.EditBeads) {
    return <SelectedBeads />;
  }

  return <React.Fragment />;
};

const SelectedBeads = () => {
  const showToolbar = useSelector((state: Editor) => {
    return state.ui.selectedBeadsIdsCount >= 1;
  });
  if (showToolbar) {
    return <DisplayPickColorToolbar />;
  }
  return <React.Fragment />;
};

const DisplayPickColorToolbar = () => {
  const dispatch = useDispatch();
  const colors = ["#e3dec1", "#e5a947", "#7a514a"];
  const onClick = color => {
    dispatch(Actions.Toolbar.action_changeBeadFill(color));
  };
  const selectedColors = useSelector((state: Editor) => {
    return Array.from(
      new Set(
        Object.keys(state.ui.selectedBeadsIds).map(beadId => {
          return state.appData.beads[beadId].fill;
        })
      )
    );
  });
  return (
    <React.Fragment>
      <DisplaySelectedColor selectedColors={selectedColors} />
      <DisplayAvailableColors
        colors={colors}
        selectedColors={selectedColors}
        onClick={onClick}
      />
    </React.Fragment>
  );
};

const DisplaySelectedColor = ({ selectedColors }) => {
  return (
    <div className="fieldset">
      <div className="label">Wypenienie:</div>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "flex-start",
          alignItems: "center",
          boxSizing: "border-box",
          paddingLeft: "10px",
          paddingRight: "20px",
          width: "100%",
        }}
      >
        {selectedColors.map(hex => {
          return (
            <DisplayColor
              background={hex}
              key={hex}
              isSelected={false}
              onClick={undefined}
            />
          );
        })}
      </div>
    </div>
  );
};

const DisplayAvailableColors = ({ colors, selectedColors, onClick }) => {
  return (
    <div className="fieldset">
      <div className="label">Kolory:</div>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "flex-start",
          alignItems: "center",
          boxSizing: "border-box",
          paddingLeft: "10px",
          paddingRight: "20px",
          width: "100%",
        }}
      >
        {colors.map(hex => {
          return (
            <DisplayColor
              background={hex}
              key={hex}
              isSelected={selectedColors.indexOf(hex) !== -1}
              onClick={onClick}
            />
          );
        })}
      </div>
    </div>
  );
};

const DisplayColor = ({ background, isSelected, onClick }) => {
  return (
    <div style={{ border: "5px solid transparent" }}>
      <div
        onClick={onClick ? () => onClick(background) : undefined}
        style={{
          width: 25,
          height: 25,
          background: background,
          marginBottom: 0,
          marginRight: 0,
          border: isSelected ? "2px solid #000" : "2px solid #000",
          boxShadow: isSelected
            ? "2px 2px 2px rgba(0,0,0,1)"
            : "2px 2px 2px rgba(0,0,0,0.1)",
        }}
      ></div>
    </div>
  );
};
