import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { Actions } from "../../store";
import { DraggableData } from "react-draggable";
import { DisplayGroupLines } from "./DisplayGroupLines";
import { getGroupPositionFromLines, getSelectedLines } from "./getGroupPositionFromLines";

export const DisplayLinesGroup = ({ selectedLinesIdsCount }) => {
  const dispatch = useDispatch();
  const selectedLines = useSelector(getSelectedLines);
  const location = getGroupPositionFromLines(selectedLines);
  const eventDragHandler = (_, { deltaX, deltaY }: DraggableData) => {
    dispatch(Actions.Draggable.action_onGroupLineDrag({ deltaX, deltaY }));
  };
  return <DisplayGroupLines location={location} onDrag={eventDragHandler} />;
};

export const MemoizedDisplayLinesGroup = React.memo(DisplayLinesGroup);
