import { Editor } from "../../editorObjects/Editor";

export const getGroupPositionFromLines = (selectedLines): { x; y; w; h } => {
  let xMin: number = Number.MAX_SAFE_INTEGER;
  let xMax: number = Number.MAX_SAFE_INTEGER * -1;
  let yMin: number = Number.MAX_SAFE_INTEGER;
  let yMax: number = Number.MAX_SAFE_INTEGER * -1;
  selectedLines.forEach(line => {
    const start = line.start;
    const end = line.end;
    xMin = Math.min(xMin, start.x, end.x);
    xMax = Math.max(xMax, start.x, end.x);
    yMin = Math.min(yMin, start.y, end.y);
    yMax = Math.max(yMax, start.y, end.y);
  });
  return {
    x: xMin,
    y: yMin,
    w: xMax - xMin + 10,
    h: yMax - yMin + 10,
  };
};

export const getSelectedLines = (state: Editor) => {
  return Object.keys(state.ui.selectedLinesIds).map(lineId => {
    return state.appData.lines[lineId];
  });
};
