import { immerable } from "immer";
import { generate } from "shortid";
import { Point, Bead, BeadTypes } from "./Editor";

export const build = (position: Point, type?: BeadTypes): Bead => {
  const id = generate();
  const beadType = type ? type : BeadTypes.BasicCircle;
  const common = {
    id,
    position,
    [immerable]: true,
    type: beadType,
  };

  if (beadType === BeadTypes.BasicCircle) {
    return buildBead_BasicCircle(common);
  }

  if (beadType === BeadTypes.BasicRectangle) {
    return buildBead_BasicRectangle(common);
  }

  return buildBead_BasicCircle(common);
};

const buildBead_BasicCircle = common => {
  return {
    ...common,
    width: 20,
    height: 20,
    fill: `linear-gradient(
      to bottom,
      rgba(254, 204, 177, 1) 0%,
      rgba(241, 116, 50, 1) 50%,
      rgba(234, 85, 7, 1) 51%,
      rgba(251, 149, 94, 1) 100%
    )`,
    settings: {
      radius: 20,
    },
  } as Bead;
};

const buildBead_BasicRectangle = common => {
  return {
    ...common,
    width: 20,
    height: 40,
    fill: "#b1b1b1",
  } as Bead;
};
