import React from "react";
import { EditorMode, Editor, BeadTypes } from "../../editorObjects/Editor";
import { useSelector, useDispatch } from "react-redux";
import { Actions } from "../../store";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircle, faExpand } from "@fortawesome/free-solid-svg-icons";

export const PickBeadType = ({ mode }) => {
  if (mode === EditorMode.EditBeads) {
    return <PickBeadTypeToolbar />;
  }

  return <React.Fragment />;
};

const PickBeadTypeToolbar = () => {
  const showToolbar = useSelector((state: Editor) => {
    return state.ui.selectedBeadsIdsCount >= 1;
  });

  if (showToolbar) {
    return <DisplayPickTypeToolbar />;
  }
  return <React.Fragment />;
};

const DisplayPickTypeToolbar = () => {
  const dispatch = useDispatch();
  const types = [
    {
      type: BeadTypes.BasicCircle,
      icon: "circle",
    },
    {
      type: BeadTypes.BasicRectangle,
      icon: "rectangle-wide",
    },
  ];
  const onClick = type => {
    dispatch(Actions.Toolbar.action_changeBeadType(type));
  };
  const selectedTypes = useSelector((state: Editor) => {
    return Array.from(
      new Set(
        Object.keys(state.ui.selectedBeadsIds).map(beadId => {
          return state.appData.beads[beadId].type;
        })
      )
    );
  });
  return (
    <React.Fragment>
      <DisplaySelectedTypes selectedTypes={selectedTypes} />
      <DisplayAvailableTypes
        types={types}
        selectedTypes={selectedTypes}
        onClick={onClick}
      />
    </React.Fragment>
  );
};

const DisplaySelectedTypes = ({ selectedTypes }) => {
  return (
    <div className="fieldset">
      <div className="label">Rodzaj:</div>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "flex-start",
          alignItems: "center",
          boxSizing: "border-box",
          paddingLeft: "10px",
          paddingRight: "20px",
          width: "100%",
        }}
      >
        {selectedTypes.map((type, index) => {
          return (
            <DisplayType
              icon={type.icon}
              type={type.type}
              key={index}
              isSelected={false}
              onClick={undefined}
            />
          );
        })}
      </div>
    </div>
  );
};

const DisplayAvailableTypes = ({ types, selectedTypes, onClick }) => {
  return (
    <div className="fieldset">
      <div className="label">Rodzaje:</div>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "flex-start",
          alignItems: "center",
          boxSizing: "border-box",
          paddingLeft: "10px",
          paddingRight: "20px",
          width: "100%",
        }}
      >
        {types.map((type, index) => {
          return (
            <DisplayType
              icon={type.icon}
              type={type.type}
              key={index}
              isSelected={selectedTypes.indexOf(type.type) !== -1}
              onClick={onClick}
            />
          );
        })}
      </div>
    </div>
  );
};

const DisplayType = ({ icon, type, isSelected, onClick }) => {
  return (
    <div style={{ border: "5px solid transparent" }}>
      <div
        onClick={onClick ? () => onClick(type) : undefined}
        style={{
          width: 25,
          height: 25,
          background: "#eee",
          marginBottom: 0,
          marginRight: 0,
          border: isSelected ? "2px solid #000" : "2px solid #000",
          boxShadow: isSelected
            ? "2px 2px 2px rgba(0,0,0,1)"
            : "2px 2px 2px rgba(0,0,0,0.1)",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        {icon === "circle" ? <FontAwesomeIcon icon={faCircle} /> : null}
        {icon === "rectangle-wide" ? <FontAwesomeIcon icon={faExpand} /> : null}
      </div>
    </div>
  );
};
